import React from 'react';
import url from 'url';
import {inject, observer} from 'mobx-react';
import {Breadcrumb, Col, Row, Select, Table, Card, Menu, Dropdown, Button, Icon, ConfigProvider} from 'antd';
import {Scrollbars} from 'react-custom-scrollbars';
import ReactEcharts from "echarts-for-react";
import moment from "moment";
import StaffStatusStore from '@/store/kanban/StaffStatusStore';


const customizeRenderEmpty = () => (
	<div style={{ textAlign: 'center',padding: '50px 0'}}>
		<img
			width='150px'
			height="100px"
			src='./images/not_found.png' />
		<p style={{
			fontSize:'14px',
			fontFamily:'PingFangSC-Regular,PingFang S',
			fontWeight: '400',
			color: 'rgba(75,80,81,1)'
		}}>暂无数据</p>
	</div>
);

@inject('kanbanStore')
@observer
export default class StaffStatus extends React.Component {
	constructor(props) {
		super(props);
		this.store = new StaffStatusStore();
		this.state = {
			id: '41', // 表id
			key: '4',
			time: null,
			value: '年度',
			value1: '2019',
			month: '', //月份
			yearData: [{
				id: '',
				name: '2019'
			}],  // 年份
			selData: [
				{
					id: '01',
					name: '1月'
				},
				{
					id: '02',
					name: '2月'
				},
				{
					id: '03',
					name: '3月'
				}, {
					id: '04',
					name: '4月'
				}
				, {
					id: '05',
					name: '5月'
				}
				, {
					id: '06',
					name: '6月'
				}
				, {
					id: '07',
					name: '7月'
				}, {
					id: '08',
					name: '8月'
				}, {
					id: '09',
					name: '9月'
				}, {
					id: '10',
					name: '10月'
				}, {
					id: '11',
					name: '11月'
				}, {
					id: '12',
					name: '12月'
				},/* {
					id: 'season1',
					name: '1季度'
				}, {
					id: 'season2',
					name: '2季度'
				}, {
					id: 'season3',
					name: '3季度'
				}, {
					id: 'season4',
					name: '4季度'
				}, {
					id: 'fullyear',
					name: '年度'
				}*/
			]
		}
		;
	}

	componentDidMount() {
		// 获取当前年份数据
		const currentYear = moment().year();
		/*
		* 年份数据来源：从2019年开始， 截至到当前的年份
		* */
		if (currentYear<=2019) {
			this.setState({
				yearData: [{
					name: '2019'
				}]
			});
		} else {
			const num = Number(currentYear);
			let data = [];
			for (let i=2019;i<=num;i++) {
				data.push({name:String(i)})
			}
			this.setState({
				yearData: Object.assign([], data.reverse())
			});
		}
		const data = {
			'01': '1月',
			'02': '2月',
			'03': '3月',
			'04': '4月',
			'05': '5月',
			'06': '6月',
			'07': '7月',
			'08': '8月',
			'09': '9月',
			'10': '10月',
			'11': '11月',
			'12': '12月'
		}
		let search = this.props.location.search;
		const id = url.parse(search, true).query.id;
		const key = url.parse(search, true).query.key;
		const time = url.parse(search, true).query.time;
		const mont = moment().month() + 1;
		const mon = url.parse(search, true).query.month;
		let month = mon == undefined ? mont : mon;
		let lastTime = null; // 最后更新时间

		// 没有时间
		if(time==undefined) {
			// 根据id查询时间
			this.getLastUpdatedTime().then((res) => {
				if (res) {
					let capitalInfo = res.filter(item => {
						return item.code === "ryqk"
					});
					if (capitalInfo.length > 0) {
						lastTime = capitalInfo[0]['lastUpdateTime'];
						this.setState({
							time: Number(lastTime)
						})
					}
				}
			});
		} else {
			this.setState({
				time: Number(time)
			})
		}

		this.setState({
			id,
			key,
			value: data[month],
			month: month.toString(),
			value1: currentYear.toString()
		}, () => {
			// 获取初始数据
			this.handleChange();
		});
	}

	// 获取最后时间
	getLastUpdatedTime = () => {
		const params = {
			"level": 2,
			"parentId": ""
		}
		return this.store.getReportClassifys(params);
	}

	handleChange = () => {
		const {month, value1, id} = this.state;
		let params = {"classifyId": id, "year": value1, "beginMonth": month, "endMonth": month};
		if (month === "season1") {
			params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": "03"};
		} else if (month === "season2") {
			params = {"classifyId": id, "year": value1, "beginMonth": "04", "endMonth": "06"};
		} else if (month === "season3") {
			params = {"classifyId": id, "year": value1, "beginMonth": "07", "endMonth": "09"};
		} else if (month === "season4") {
			params = {"classifyId": id, "year": value1, "beginMonth": "10", "endMonth": "12"};
		} else if (month === "fullyear") {
			params = {"classifyId": id, "year": value1, "beginMonth": "01", "endMonth": "12"};
		}
		this.getReportByTime(params);
	}

	onChange = () => {

	}

	onSelect = d => {
		console.log('select')
		console.log(d);
	}

	disabledDate = (current) => {
		console.log(current, '当前')
		//return current && current.valueOf() > Date.now();
	}

	getReportByTime = (params) => {
		this.renderData(params);
	}

	fillTableData = (item) => {
		if (item.name === "各部门人员分布") {
			if (item.schemas) {
				this.store.archTableColumn = item.schemas;
			}
			if (item.datas) {
				this.store.archTableData = item.datas;
				let chartsData = item.datas.filter(item => {
					return item.post !== '合计'
				})
				this.store.archChartData = chartsData.map(item => {
					return Object.assign({}, {'value': item.peopleCount}, {'name': item.post})
				})
			}

		}
		if (item.name === "司龄及性别分布") {
			if (item.schemas) {
				this.store.ageSexTableColumn = item.schemas;
			}
			if (item.datas) {
				this.store.ageSexTableData = item.datas;
				let chartsData = item.datas.filter(item => {
					return item.seniority !== '合计'
				})
				this.store.ageSexChartData = chartsData.map(item => {
					return Object.assign({}, {'value': item.total}, {'name': item.seniority})
				})
			}
		}
	}

	renderData = (params) => {
		this.store.getReportByClassifyId(params).then((res) => {
			if (res) {
				res.map((item, index) => {
					this.fillTableColumn(item) //填充表头数据
				})
			}
			return res
		}).then((res) => {
			if (res) {
				res.map((item, index) => {
					this.fillTableData(item) // 填充表体数据
					this.fillChartData(item) // 填充图表数据
				})
			}
		});
	}

	fillTableColumn = (item) => {
		if (item.name === "各部门人员分布") {
			if (item.schemas) {
				this.store.archTableColumn = item.schemas.map(item => {
					return Object.assign({}, {'dataIndex': item.name}, {'title': item.title}, {'align': 'center'})
				});
			}
		}
		if (item.name === "司龄及性别分布") {
			if (item.schemas) {
				this.store.ageSexTableColumn = item.schemas.map(item => {
					return Object.assign({}, {'dataIndex': item.name}, {'title': item.title}, {'align': 'center'})
				});
			}
		}
	}

	fillTableData = (item) => {
		if (item.name === "各部门人员分布") {
			if (item.datas) {
				this.store.archTableData = item.datas;
			}

		}
		if (item.name === "司龄及性别分布") {
			if (item.datas) {
				this.store.ageSexTableData = item.datas;
			}
		}
	}

	fillChartData(item) {
		if (item.name === "各部门人员分布") {
			if (item.charts) {
				this.store.archLegendData = item.charts.category;
				this.store.archChartData = item.charts.series
			}
		}
		if (item.name === "司龄及性别分布") {
			if (item.charts) {
				this.store.ageSexLegendData = item.charts.category;
				this.store.ageSexChartData = item.charts.series
			}
		}
	}

	handleMenuClick = (e) => {
		let value = e.key;
		this.setState({
			value: e.item.props.children,
			month: value
		}, ()=>{
			this.handleChange();
		});
	}

	// 切换年份
	handleYearClick = (e) => {
		this.setState({
			value1: e.item.props.children
		},()=>{
			this.handleChange();
		})
	}

	totalFontBold = (text, row, index) => {
		if (text === "合计") {
			return {
				children: <span>{text}</span>,
				props: {
					className: 'tab_td_bg fwb'
				}
			};
		}
		return text
	}


	render() {
		const {yearData, selData, value, value1, key, time} = this.state;
		const menu = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleMenuClick}>
					{selData.map((item, index) => {
						return (
							<Menu.Item key={item.id}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
		);
		const menu1 = (
			<Scrollbars style={{height: 200}}>
				<Menu className="con_menu" onClick={this.handleYearClick}>
					{yearData.map((item, index) => {
						return (
							<Menu.Item key={'year' + index}>
								{item.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</Scrollbars>
		);
		const columns2 = [
			{
				title: '部门名称',
				dataIndex: 'post',
				align: 'center',
				className: 'vt',
			},
			{
				title: '人数',
				dataIndex: 'peopleCount',
				align: 'center',
				className: 'vt',
			},
			{
				title: '占比',
				dataIndex: 'percentage',
				align: 'center',
				className: 'vt',
			}, {
				title: '职级结构',
				dataIndex: 'phone',
				align: 'center',
				children: [
					{
						title: 'M6/P6',
						dataIndex: 'mp6',
						align: 'center',
						className: 'vt th_bg1',
					},
					{
						title: 'M5/P5',
						dataIndex: 'mp5',
						align: 'center',
						className: 'vt th_bg1',
					},
					{
						title: 'M4/P4',
						dataIndex: 'mp4',
						align: 'center',
						className: 'vt th_bg1',
					},
					{
						title: 'P3',
						dataIndex: 'p3',
						align: 'center',
						className: 'vt th_bg1',
					},
					{
						title: 'P2',
						dataIndex: 'p2',
						align: 'center',
						className: 'vt th_bg1',
					},
				]
			}
		];
		const columns3 = [{
			title: '司龄',
			dataIndex: 'seniority',
			align: 'center',
			className: 'vt',
			// render:this.totalFontBold
		},
			{
				title: '人数',
				dataIndex: 'phone',
				align: 'center',
				children: [
					{
						title: '男',
						dataIndex: 'man',
						align: 'center',
						className: 'vt th_bg1',
					},
					{
						title: '女',
						dataIndex: 'woman',
						align: 'center',
						className: 'vt th_bg1',
					},
					{
						title: '合计',
						dataIndex: 'total',
						align: 'center',
						className: 'vt th_bg1',
					},
					{
						title: '占比',
						dataIndex: 'percentage',
						align: 'center',
						className: 'vt th_bg1',
					}
				]
			}
		];

		var option1 = {
			title: {
				text: '各部门人员分布情况',
				left: 'center',
				textStyle:{
					color: 'rgb(61, 77, 102)',
					fontWeight:'bold',
					fontStyle:'normal',
					fontFamily: 'Microsoft YaHei UI',
					fontSize:'13'
				}
			},
			tooltip: {
				trigger: 'item',
				formatter: "{b} : {c} <br/>{a} : {d}%"
			},
			grid: {
				left: '3%',
				right: '3%',
				bottom: '15%',
				containLabel: true
			},
			legend: {
				bottom: 0,
				data: this.store.archLegendData,
			},
			toolbox: {
				show: true,
				feature: {
					mark: {show: true},
					dataView: {show: false, readOnly: false},
					magicType: {
						show: true,
						type: ['pie', 'funnel'],
						option: {
							funnel: {
								x: '25%',
								width: '50%',
								funnelAlign: 'left',
								max: 1548
							}
						}
					},
					restore: {show: false},
					saveAsImage: {show: false}
				}
			},
			calculable: true,
			series: [
				{
					name: '占比',
					type: 'pie',
					radius: ['40%', '65%'],
					center: ['50%', '50%'],
					label: {
						normal: {
							show: false,
							formatter: "{d}%",
							position: 'inner',
							textStyle: {
								color: '#FFF',
								fontSize: 12
							}
						}
					},
					data: this.store.archChartData,
					color: ['#87BDFA', '#9C96F1', '#63C8C6', '#BADBF6', '#AEDFF1', '#F2D6BC', '#8EABD0', '#47BAC1' ]
				}
			]
		};

		var option2 = {
			title: {
				text: '司龄分布情况',
				left: 'center',
				textStyle:{
					color: 'rgb(61, 77, 102)',
					fontWeight:'bold',
					fontStyle:'normal',
					fontFamily: 'Microsoft YaHei UI',
					fontSize:'13'
				}
			},
			tooltip: {
				trigger: 'item',
				formatter: "{b} : {c} <br/>{a} : {d}%"
			},
			legend: {
				bottom: 0,
				data: this.store.ageSexLegendData,
			},
			toolbox: {
				show: true,
				feature: {
					mark: {show: true},
					dataView: {show: false, readOnly: false},
					magicType: {
						show: true,
						type: ['pie', 'funnel'],
						option: {
							funnel: {
								x: '25%',
								width: '50%',
								funnelAlign: 'left',
								max: 1548
							}
						}
					},
					restore: {show: false},
					saveAsImage: {show: false}
				}
			},
			calculable: true,
			series: [
				{
					name: '占比',
					type: 'pie',
					radius: ['40%', '65%'],
					center: ['50%', '50%'],
					label: {
						normal: {
							show: false,
							formatter: "{d}%",
							position: 'inner',
							textStyle: {
								color: '#FFF',
								fontSize: 12
							}
						}
					},
					data: this.store.ageSexChartData,
					color: ['#47BAC1','#7FC8EA','#B5E3E5','#6DD4B3','#A6E59F', '#59BFCB',],
				}
			]
		};

		const {defaultKey} = this.props.kanbanStore;


		return (
			<div>
				<ConfigProvider renderEmpty={customizeRenderEmpty}>
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item href={"#/home?key=" + defaultKey}>经营看板</Breadcrumb.Item>
						<Breadcrumb.Item href={"#/home?key=" + key}>人力</Breadcrumb.Item>
						<Breadcrumb.Item>人员情况表</Breadcrumb.Item>
					</Breadcrumb>
				</div>
				<Row>
					<Col span={24}>
						<div className="mt20 mb20">
							<span className="con_title">人员情况表 </span>
							<span className="con_time ml35">最近更新时间:</span><span
							className="con_time_content ml15">{moment(time).format("YYYY-MM-DD HH:mm:ss")}</span>
						</div>
					</Col>
					<Col span={24}>
						<Dropdown overlay={menu1}>
							<Button style={{width: 82}}>
								{value1} <Icon type="down"/>
							</Button>
						</Dropdown>
						<Dropdown overlay={menu}>
							<Button className="ml15" style={{width: 82}}>
								{value} <Icon type="down"/>
							</Button>
						</Dropdown>
					</Col>
				</Row>

				<Card bordered className="mt20" style={{width: '100%'}}>
					<h3 className="kanban_title mb20">人才结构情况（单位：人）</h3>
					<Row>
						<Col span={10}>
							<ReactEcharts
								notMerge={true}
								style={{height:'382px'}}
								lazyUpdate={true}
								option={option1}/>
						</Col>
						<Col span={14}>
							<Table columns={columns2}
							       dataSource={this.store.archTableData}
							       size={"small"}
							       pagination={false}
							       title={() => '各部门人员分布情况'}
							       className="ci_table2 ci_table5"
							       rowClassName={(record, index) => {
								       let className = index % 2 ? 'shallow_gray' : 'deep_gray_1';
								       return className
							       }}
							       bordered/>
						</Col>
					</Row>
				</Card>
				<Card bordered className="mt20" style={{width: '100%'}}>
					<h3 className="kanban_title mb20">司龄及性别分布（单位：人）</h3>
					<Row>
						<Col span={10}>
							<ReactEcharts
								notMerge={true}
								lazyUpdate={true}
								style={{height:'314px'}}
								option={option2}/>
						</Col>
						<Col span={14}>
							<Table columns={columns3}
							       dataSource={this.store.ageSexTableData}
							       size={"small"}
							       pagination={false}
							       title={() => '司龄及性别分布明细'}
							       className="ci_table2 ci_table5"
							       rowClassName={(record, index) => {
								       let className = index % 2 ? 'shallow_gray' : 'deep_gray_1';
								       return className
							       }}
							       bordered/>
						</Col>
					</Row>
				</Card>
				</ConfigProvider>
			</div>
		);
	}
}





